* {
  box-sizing: border-box;
}
body {
  background-color: rgb(243, 135, 153);
}

.container {
  width: 500px;
  margin: auto;
  background-color: white;
  border-radius: 10px;
  padding: 15px;
  margin-top: 10vh;
  box-shadow: rgb(228, 63, 51) 10px 10px 10px;
}
.BirthdayList {
  list-style: none;
  justify-content: left;
}
.BirthdayList li {
  position: relative;
  width: 100%;
  border-radius: 10px;
  padding: 10px;
  right: 17px;
  height: 80px;
  margin: 10px;
  left: -30px;
}
.btn {
  width: 100%;
  margin: auto;
  padding: 10px;
  background-color: rgb(243, 135, 153);
  border-radius: 5px;
  border-width: transparent;
  border-color: transparent;
  font-size: 20px;
  color: white;
  font-weight: bold;
}

.btn:hover {
  background-color: rgb(230, 93, 116);
}

.btn2 {
  position: relative;
  left: 324px;
  top: -68px;
  border-width: transparent;
  border-color: transparent;
  color: rgb(243, 135, 153);
  letter-spacing: 2px;
  background-color: inherit;
}
.datacontainer {
  width: 100%;
}
.div {
  display: inline-block;
}
.imageContainer {
  width: 30%;
  position: relative;
  top: -7px;
  object-fit: cover;
}
.imageContainer img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  object-fit: cover;
}
.DetailsContainer {
  width: 60%;
  position: relative;
  line-height: 2px;
  top: -25px;
}

h3 {
  font-size: 20px;
  font-weight: bold;
}
p {
  color: rgb(243, 135, 153);
}
